body {
    margin: 0px;
    padding: 0px;
    border: 0px;
    background-color: rgba(0, 0, 0, 0.932);
}

.container {
    display: flex;
    width: 100%;
    margin-top: 40px;
}

.item {
    display: flex;
    flex-wrap: nowrap;
}


.loading {
    height: 50px;
    width: 50px;
    /*요소의 높이, 너비를 50px로 설정*/
    position: absolute;
    /*부모 요소를 기준으로 위치를 설정, 부모 요소가 relative로 설정되어있어야함.*/
    right: 50%;
    /*요소의 오른쪽 모서리가 부모 요소의 너비의 50% 위치에 오도록 설정*/
    bottom: 50%;
    /*요소의 하단이 부모 요소의 높이의 50%위치에 오도록 설정, right, bottom을 이용해서 가운데에 위치 시킴*/
    border: 3px solid #37474f;
    border-radius: 50%;
    /*모서리가 완만해짐*/
    border-top-color: #ff9800;
    /*테두리의 상단 색상을 설정 #ff9800(주황색)으로 설정하여 로딩 애니메이션에서 회전하는 부분의 색상*/
    animation: spin 0.75s ease-in-out infinite;
    /*애니메이션, spin이라는것을 1초 동안 시작과 끝은 느리게 중간은 빠르게 무한 반복된다.*/
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}