.items {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    background-color: #212121;
    border: 1px solid #37474f;
    border-radius: 25px;
    margin: 0 45px;
}

.item1 {
    width: 232px;
    /* 너비를 232픽셀로 설정 */
    height: 347px;
    /* 높이를 347픽셀로 설정 */
    border: 1px solid #37474f;
    margin: 30px 30px 20px;

}

.item2 {
    font-size: 1rem;
    text-align: center;
    width: 350px;
    margin: 0;
    margin-bottom: 20px;
    font-weight: lighter;
}

.link {
    color: #ff9800;
    text-decoration: none;
}

.link:hover {
    text-decoration: underline;
}


.item3 {
    width: 350px;
    /* 너비를 500픽셀로 설정 */
    color: white;
    margin: 0 10px;
    font-weight: lighter;
}

.item4 {
    align-self: flex-start;
    color: white;
    padding-left: 30px;
    margin-bottom: 20px;
    font-weight: lighter;
}